import './App.css';
import bg from "./bg.jpg";
import { useState } from "react";

function App() {
  const QA = [
      {
          q: 'Lorem ipsum',
          a: 1,
          n: 8,
      },
      {
          q: 'Lorem ipsum',
          a: 2,
          n: 3,
      },
      {
          q: 'Lorem ipsum',
          a: 3,
          n: 2,
      }
  ]

  const [started, setStarted] = useState(false)
  const [finished, setFinished] = useState(false)
  const [qIndex, setQIndex] = useState(0)

  const handleAnswer = () => {
    if((qIndex + 1) > (QA.length - 1)) {
        setFinished(true)
    } else {
        setQIndex(qIndex + 1)
    }
  }

  return (
    <div className="App" style={{backgroundImage: `url(${bg})`, backgroundSize: '25%'}}>
        <div id='main'>
            {!started && (
                <>
                    <h2>Odpowiedz na pytania i zgadij swój kod do sejfu!</h2>
                    <button onClick={() => setStarted(true)}>Start</button>
                </>
            )}
            {(started && !finished) && (
                <>
                    <h2>{QA[qIndex].q}</h2>
                    <button onClick={handleAnswer}>Next question</button>
                </>
            )}
            {finished && (
                <>
                    <h2>Gratki! Pełny kod to:</h2>
                    <h1>832</h1>
                </>
            )}
        </div>
    </div>
  );
}

export default App;
